<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="1"><div class="grid-content"></div></el-col>
      <el-col :span="22"><div class="grid-content">
        <el-table v-loading="loading" :data="lists" style="width: 100%" height="600">
          <el-table-column align="center" prop="id" label="ID" width="50" fixed></el-table-column>
          <el-table-column align="center" prop="order_id" label="订单号"></el-table-column>
          <el-table-column align="center" prop="duration" label="播放时长" :formatter="addSeconds"></el-table-column>
          <el-table-column align="center" prop="play_num" label="保底播放次数"></el-table-column>
          <el-table-column align="center" prop="start_time" label="开始时间" width="150" :formatter="dateFormat"></el-table-column>
          <el-table-column align="center" prop="end_time" label="结束时间" width="150" :formatter="dateFormat"></el-table-column>
          <el-table-column align="center" label="是否确认">
            <template v-slot="scope">
              <el-button v-if="scope.row.confirmed===1" type="success" icon="el-icon-check" size="mini" circle></el-button>
              <el-button v-if="scope.row.confirmed===0" type="warning" icon="el-icon-loading" size="mini" circle></el-button>
            </template>
          </el-table-column>
          <el-table-column align="center" label="是否支付">
            <template v-slot="scope">
              <el-button v-if="scope.row.payed===1" type="success" icon="el-icon-check" size="mini" circle></el-button>
              <el-button v-if="scope.row.payed===-1" type="warning" icon="el-icon-loading" size="mini" circle></el-button>
              <el-button v-if="scope.row.payed===0" type="danger" icon="el-icon-close" size="mini" circle></el-button>
            </template>
          </el-table-column>
          <el-table-column align="center" label="是否发布">
            <template v-slot="scope">
              <el-button v-if="scope.row.release===1" type="success" icon="el-icon-check" size="mini" circle></el-button>
              <el-button v-if="scope.row.release===-1" type="warning" icon="el-icon-loading" size="mini" circle></el-button>
              <el-button v-if="scope.row.release===0" type="danger" icon="el-icon-close" size="mini" circle></el-button>
            </template>
          </el-table-column>
          <el-table-column align="center" prop="created_at" label="创建时间" width="150"></el-table-column>
          <el-table-column align="center" prop="updated_at" label="更新时间" width="150"></el-table-column>
        </el-table>
      </div></el-col>
    </el-row>
    <Pagination />
  </div>
</template>

<script>
import Pagination from '@/components/layout/Pagination'
import { lists } from '@/api/mo/order'
import { datetime } from '@/utils/date'

export default {
  data () {
    return {
      lists: [],
      loading: true
    }
  },
  created () {
    lists({}).then(response => {
      this.lists = []
      response.data.list.forEach(item => {
        this.lists.push(item)
      })
      this.loading = false
    })
  },
  components: {
    Pagination
  },
  methods: {
    dateFormat (row, column, cellValue, index) {
      return datetime(row[column.property])
    },
    addSeconds (row, column, cellValue, index) {
      return row[column.property] + 's'
    }
  }
}
</script>

<style>
  .el-row {
    margin-bottom: 20px;
  }
  .el-row:last-child {
    margin-bottom: 0;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>



