<template>
  <el-row :gutter="20">
    <el-col :span="1"><div class="grid-content"></div></el-col>
    <el-col :span="22">
      <div class="block">
        <el-pagination
          layout="prev, pager, next"
          :page-size="pageSize"
          :total="total"
          :current-page="currentPage"
          @prev-click="jumpPage"
          @next-click="jumpPage"
          @current-change="jumpPage"
        ></el-pagination>
      </div>
    </el-col>
    <el-col :span="1"><div class="grid-content"></div></el-col>
  </el-row>
</template>

<script>
export default {
  props: {
    total: Number,
    currentPage: Number,
    pageSize: Number
  },
  // 需要绑定分页数据
  data () {
    return {
    }
  },
  methods: {
    // 跳转页面
    jumpPage (page) {
      this.$emit('to-page', page)
    }
  }
}
</script>

<style scoped>
  .block {
    width: 100%;
    background-color: #fff;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
</style>



